	        rate_area: function rate_area(now_rate) {
	            var _this = this;
	            if (now_rate - 25 <= 0) {
	                _this.start_rate = 0;
	                _this.end_rate = 50;
	            } else if (now_rate + 25 >= 100) {
	                _this.start_rate = 50;
	                _this.end_rate = 100;
	            } else {
	                _this.start_rate = now_rate - 25;
	                _this.end_rate = now_rate + 25;
	            }
	        },
